.Main {
    width: 100%;
    height: 100%;
}
.Main .container {
    width: 600px;
    height: 100%;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.titleSection {
    margin-top: 230px;
}
.stepBtn {
    width: 250px;
    height: 43px;
    background: #A70000;
    color: #FFFFFF;
    border: none;
    margin-top: 30px;
    font-family: 'Helvetica';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 15px;
    letter-spacing: 0.02em;
    cursor: pointer;
}
.noticeText {
    font-family: 'Helvetica';
    font-style: normal;
    font-weight: 300;
    font-size: 22px;
    line-height: 25px;
    text-align: center;
    letter-spacing: 0.03em;
    color: #FFFFFF;
}

.stepSection {
    width: 100%;
    margin-top: 80px;
    cursor: pointer;
}
.Step01 {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.Step02 {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.Step03 {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.inputWrap {
    margin-top: 30px;
    display: flex;
}
.inputWrap .inputEmail {
    box-sizing: border-box;
    padding: 15px 25px;
    width: 350px;
    border: 1px solid #A70000;
    color: #FFFFFF;
    background-color: inherit;
    font-family: 'Helvetica';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 15px;
    letter-spacing: 0.02em;
}
.inputWrap .inputEmail:focus {
    outline: none;
}
.inputWrap .submitBtn {
    width: 146px;
    font-size: 16px;
    color: #FFFFFF;
    border: none;
    background: #A70000;
    margin-left: 10px;
    cursor: pointer;
}
.checkwrap {
    width: 50px;
    height: 50px;
    background: #A70000;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
    border-radius: 50px;
}
@media screen and (max-width:1000px) {
    .Main .container {
        width: 100%;
        padding: 16px;
        box-sizing: border-box;
    }
    .inputWrap {
        margin-top: 30px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

    }
    .inputWrap .submitBtn {
        width: 100%;
        padding: 15px 0;
        font-size: 16px;
        color: #FFFFFF;
        border: none;
        background: #A70000;
        margin-left: 0px;
        margin-top: 10px;
        cursor: pointer;
    }
    .titleSection {
        margin-top: 0px;
    }
}
